@font-face {
  font-family: "Helvetica";
  src: url("../fonts/Helvetica.woff2") format("woff2"), url("../fonts/Helvetica.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Helvetica";
  src: url("../fonts/Helvetica-Bold.woff2") format("woff2"), url("../fonts/Helvetica-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  height: 100%;
  font-family: "Helvetica", "Arial", sans-serif;
}

.page__body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  font-size: 24px;
  line-height: 24px;
  background-color: #ffffff;
}

.main-container {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  padding: 8px 34px;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  color: #231f20;
  background-color: #61e78d;
  font-family: "Helvetica", "Arial", sans-serif;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.main-header {
  background-color: #51c277;
}

.main-header__wrapper {
  position: relative;
  width: 100%;
  padding: 0 22px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #000000;
  box-sizing: border-box;
}

.main-header__logo {
  margin-left: auto;
  margin-right: auto;
}

.main-header__logo-image {
  display: block;
}

@media (min-width: 768px) {
  .main-header {
    background-color: transparent;
    display: flex;
  }
  .main-header__wrapper {
    padding: 22px 40px;
    align-items: center;
    justify-content: flex-start;
    border-bottom: none;
  }
  .main-header__logo {
    margin: 0 30px 0 0;
  }
  .main-header__logo-image {
    width: 62px;
    height: 62px;
  }
}
@media (min-width: 1200px) {
  .main-header__wrapper {
    max-width: 1200px;
    padding-left: 110px;
    padding-right: 110px;
  }
  .main-header__logo {
    margin: 0 68px 0 0;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 9px;
  right: 16px;
  z-index: 2;
  display: block;
  width: 34px;
  height: 34px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 -6px 0 0 #000000;
  transform: translate(-50%, -50%);
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 4px;
  width: 27px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    display: block;
    width: 100vw;
    top: 49px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
  }
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
    position: static;
  }
}
@media (min-width: 768px) {
  .main-nav {
    flex-grow: 1;
  }
}
.nav-list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}

.nav-list__button {
  text-decoration: none;
  color: inherit;
  width: 100vw;
  display: block;
  min-height: 48px;
  padding-top: 11px;
  padding-bottom: 11px;
  margin-left: auto;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 20px;
  line-height: 25px;
  background-color: #51c277;
  border-bottom: 1px solid #000000;
  box-sizing: border-box;
}

.nav-list__item:last-child .nav-list__button {
  border-radius: 0 0 12px 12px;
}

@media (min-width: 768px) {
  .nav-list__button {
    display: inline;
    width: auto;
    background-color: transparent;
    border-bottom: none;
  }
  .nav-list__item:last-child .nav-list__button {
    border-radius: 0;
  }
  .nav-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 40px;
  }
  .nav-list__item {
    margin-bottom: 0;
  }
  .nav-list__item--right {
    margin-left: auto;
  }
  .nav-list__item .nav-list__button--enter {
    background-color: #51c277;
    padding: 14px 6px 11px;
    border-radius: 8px !important;
  }
}
@media (min-width: 1200px) {
  .nav-list {
    gap: 60px;
  }
}
