@use "../../common/forwarded.scss" as f;

.page__login {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  min-width: 320px;
  padding: 69px 0 60px;
  font-size: 24px;
  line-height: 28px;
  background-color: #ffffff;
  color: f.$color-text-second;

  @media (min-width: f.$tablet-width) {
    padding: 69px 30px 60px;
  }
}
